-template-columns:var(--ActsBox)}.GroceryShoppingRow{display:grid;grid-template-rows:1fr 1fr}.GroceryItem{display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr 1fr}.toggle{position:relative;padding:0 4px;display:flex;align-items:center}input[type="checkbox"].checkbox{width:70px;height:10px;background:#555;position:relative;border-radius:5px;-webkit-appearance:none;-moz-appearance:none;appearance:none;cursor:pointer;vertical-align:2px;outline:none;padding:6px 15px}input[type="checkbox"].checkbox:checked+label{left:45px}input[type="checkbox"].checkbox:focus-visible{outline:solid 2px white}input[type="checkbox"].checkbox+label{display:inline-block;width:30px;height:30px;border-radius:50%;-webkit-transition:all 0.3s ease;transition:all 0.3s ease;cursor:pointer;position:absolute;left:2px;background:#fff;opacity:0.9;background-color:#2a7aa8}.toggle-container{display:flex;margin:0 auto}.toggle-container>span{font-size:1.2em;background:none;border:none;cursor:pointer;-webkit-transition:color 0.3s ease;transition:color 0.3s ease;margin-top:4px;margin-right:2px}.toggle-container>span:last-child{margin-top:5px}.MuiFab-primary{color:#fff;background-color:#3f51b5 !important;justify-self:end}.MuiFab-primary:hover{color:#c5c746;background-color:#1b90cf !important}@media (max-width: 600px){#ManageFundsRoot{grid-column:1/7;min-height:39vh}.Toolbar{height:32vh;display:grid;grid-template-columns:repeat(auto-fill, minmax(100%, 1fr));grid-template-rows:5fr 5fr 5fr 5fr 1fr 8fr}#HeaderRoot{grid-column:1/7}#FundsSummaryRoot{grid-column:1/7}#FundsView{grid-column:1/7}#item4{grid-column:1/7}}.MuiIconButton-root{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}

.moonies>span:nth-child(1){color:#020080}.moonies>span:nth-child(2){color:#0f43a3}.moonies>span:nth-child(3){color:#1b2dd3}.moonies>span:nth-child(4){color:#2c23d5}.moonies>span:nth-child(5){color:#261ebe}.moonies>span:nth-child(6){color:#0f099d}.moonies>span:nth-child(7){color:#06007f}

.funditems{height:var(--actsGridHeight);border:4px solid #f4f4f4;padding:var(--Acts-Box-Padding);border-radius:12px;background-color:#f6f6f6;font-size:1.2em;color:#4b4b4b;font-weight:600;display:grid;grid-template-areas:var(--actsGrid);grid-template-columns:var(--actsGridColumns);grid-template-rows:var(--actsGridRows);box-shadow:-8px -7px 10px 0px #ffffff, 7px 8px 20px 0px #dad9d9}.fundLName{grid-area:gFN;justify-self:var(--acts-grid-area-gFN-justify);align-self:var(--acts-grid-area-gNF-align)}.fundLBalanceDate{grid-area:gDat;justify-self:var(--acts-grid-area-gDat-justify);align-self:var(--acts-grid-area-gDat-align)}.fundLBalance{grid-area:gBal;justify-self:var(--acts-grid-area-gBal-justify);align-self:var(--acts-grid-area-gBal-align);color:#8BC34A;font-size:20px;font-weight:900;letter-spacing:.55px}.fundIName{grid-area:gIna;width:100%;justify-self:var(--acts-grid-area-gIna-justify);align-self:var(--acts-grid-area-gIna-align)}.btnfundUpdate{grid-area:gbUp;justify-self:var(--acts-grid-area-gbUp-justify);align-self:var(--acts-grid-area-gbUp-align)}.btnFundRemove{grid-area:gDel;justify-self:var(--acts-grid-area-gDel-justify);align-self:var(--acts-grid-area-gDel-align)}.isActiveToggle{grid-area:gAct;justify-self:var(--acts-grid-area-gAct-justify);align-self:var(--acts-grid-area-gAct-align)}.funditems input{font-size:1vw;text-align:bottom;height:30px;border:none;border-radius:12px;width:100%;background-color:#587a8f;border:1px solid #96a2aa;padding-left:20px;padding-right:20px;text-align:right}.funditems input:focus{outline:none;background-color:white;color:blue;font-size:20px;text-align:center}

#FundsSummaryRoot{grid-area:FundsSummaryRoot;grid-column:2/6;padding:30px;min-height:300px;background-color:var(--ManageFundsRoot-bk-color);border-radius:15px;color:var(--primary-color);font-size:24px;box-shadow:-8px -7px 10px 0px #ffffff, 7px 8px 20px 0px #dad9d9}.FundsSummaryRoot{height:100%;display:grid;grid-template-columns:100px auto 100px;grid-template-rows:2fr 4fr 2fr}.FundsSummaryRoot div{grid-column:1}.FundsSummaryRoot div:nth-child(4){grid-row:2;grid-column:3;justify-self:end;align-self:center}.FundsSummaryRoot div:nth-child(1){width:100%;grid-row:3;grid-column:1/4;display:grid;grid-template-columns:repeat(3, 1fr)}.FundsSummaryRoot div:nth-child(1) button:first-child{grid-column:1;justify-self:start;align-self:end}.FundsSummaryRoot div:nth-child(1) button:last-child{grid-column:3;justify-self:end;align-self:end}.FundsSummaryRoot div:nth-child(2){grid-row:1;grid-column:1/4;font-size:34px;color:var(--ManageFundsRoot-text-color);font-weight:900;justify-self:center;align-self:center}.FundsSummaryRoot div:nth-child(3){grid-row:2;grid-column:1/4;font-size:11vw;font-weight:900;justify-self:center;align-self:center;min-height:120px;color:var(--ManageFundsRoot-text-color)}@media (max-width: 600px){#FundsSummaryRoot{grid-column:1/7}#HeaderRoot{grid-template-rows:100%}}

